}

.navigation__item {
  width: 100%;
  text-align: center;
  position: relative;
}

.navigation__item:hover > .nav-submenu, .navigation__item:focus > .nav-submenu, .navigation__item:active > .nav-submenu {
  transform: translate(-10%, 0) scale(1);
  opacity: 1;
  visibility: visible;
}

.navigation__item:hover > ul, .navigation__item:focus > ul, .navigation__item:active > ul {
  display: inherit;
}

.navigation__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
