.navigation__link:after {
  content: '';
  display: block;
  width: 50%;
  height: 2px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateX(50%);
  background-color: #b245de;
  visibility: hidden;
  opacity: 0;
}

.navigation__link:hover, .navigation__link:focus, .navigation__link:active {
  color: #000000;
}

.navigation__link:hover:after, .navigation__link:focus:after, .navigation__link:active:after {
  visibility: visible;
  opacity: 1;
}

.nav-submenu {
  display: flex;
  opacity: 0;
  visibility: hidden;
  transform-origin: bottom center;
  transform: translate(-10%, -100%) scale(0);
  position: absolute;
  top: 145%;
  left: 0;
  flex-direction: column;
  background-color: #ffffff;
  box-shadow: inset 0 0 2.5px 1px rgba(178, 69, 222, 0.5);
  padding: 1rem 0;
  z-index: 998;
  transition: all 1s ease;
}

