.section--active .pages {
  padding-left: 34%;
}

.section--active .pages__number {
  transition: all 0.3s ease;
  color: #000000;
}

.section--active .pages__number:before {
  left: -39.5%;
}

.section--wrapper {
  width: 100%;
  height: 100vh;
  position: relative;
  display: flex;
}

@media screen and (max-width: 1336px) {
  .section--active .pages {
    padding-left: 34%;
  }
}

@media screen and (max-width: 1336px) and (max-width: 1024px) {
  .section--active .pages {
    padding-left: 33.5%;
  }
}

.services-content {
  display: flex;
  width: 100%;
  background-color: #ffffff;
  z-index: 2;
  font-size: 1.25vw;
}

.services-content__legend {
  display: flex;
  color: #000000;
  line-height: 2.5rem;
  text-align: justify;
}

.services-content--wrapper {
  display: flex;
  flex-direction: column;
  width: 92.5%;
  padding: 5% 0;
}

.services-article {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 2.5% 0;
}

.services-article__title {
  font-size: 2.5vw;
}

.services-article__description {
  text-align: justify;
  line-height: 2.5rem;
  margin: 1% 0;
  color: #000000;
}

.services-article__btn {
  width: 25%;
  margin-top: 1%;
  background-color: yellow;
  padding: 1rem;
  border: 1px solid #f9f9f9;
  font-size: 1.25vw;
}

.list-description {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.list-description__item {
  display: flex;
  width: 100%;
  text-align: justify;
  margin: 1% 0;
}

.hero {
  display: flex;
  width: 100%;
  position: relative;
}

.hero__title {
  position: absolute;
  bottom: 0;
  left: 17.5%;
  transform: translateY(-50%);
  color: #ffffff;
  font-size: 4vw;
}

.hero__img {
  width: inherit;
  overflow: hidden;
}

.hero__img img {
  width: 100%;
