@import "./src/sass/helpers/var";

.social-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  margin: 0 auto;
  &--aside {
    flex-direction: column;
    width: 5%;
    height: 20vh;
    margin: 5% auto 0 0;
    position: sticky; }

  &__item {
    width: 100%;
    align-items: center;
    justify-content: center;
    display: flex; }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.5rem;
    height: 2.5rem;

    svg {
      fill: rgba($purple, 0.75);
      transition: $transition; }

    &:hover {
      svg {
        fill: rgba($purple, 1);
        transform: scale(1.05); } } } }

@media screen and (max-width: 1024px) {
  .social-menu {
    width: 20%; } }
