@import "./src/sass/helpers/var";

.about {
  width: 40%;
  display: flex;
  background-color: rgba($gray, 0.5);
  height: 100vh;

  &--wrapper {
    display: flex;
    flex-direction: column;
    padding: 12.5% 10%;
    justify-content: center;
    align-items: flex-start; }

  &__title {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 3.5rem;
    padding: 10% 0 5%; }

  &__description {
    &--wrapper {
      width: 100%;
      display: flex;
      flex-direction: column;
      padding: 5% 0;

      p {
        padding: 0.75rem 0;
        font-size: 1.25rem;
        color: $white; } } } }

@media screen and (max-width: 1336px) {
  .about {
    width: 42.5%;
    &__description {
      &--wrapper {
        p {
          font-size: 1rem; } } } } }
