@import "./src/sass/helpers/var";

.blog-box {
  display: grid;
  width: 100%;
  justify-content: space-between;
  padding: 2.5%;
  grid-template-columns: repeat(3, 32.5%);
  grid-gap: 2.5%;

  &__item {
    display: flex;
    flex-direction: column;
    background-color: $white;
    transition: $transition;
    position: relative;
    padding: 10% 5%;

    h3 {
      font-size: 2rem;
      width: 100%;
      text-align: justify;
      position: absolute;
      bottom: 15%;
      left: 0;
      padding: 5% 10%;
      color: $white; }

