    &:hover,
    &:focus,
    &:active {
      box-shadow: 0 0 30px 0 rgba($purple, 0.5); } }

  &__img {
    &:before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black, 0.25); } } }

.date-publish {
  width: 100%;
  padding: 1.25rem 0;
  position: absolute;
  top: 0;
  left: 0;
  padding: 5% 10%;
  display: flex;
  line-height: 1.25rem;

  &__number {
    margin-right: 0.5rem;
    display: block;
    line-height: 2.5rem;
    font-size: 2.5vw; } }

.categories {

  width: 100%;
  padding: 1.25rem 0;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  left: 0;
  padding: 5% 10%;
  display: flex;
  width: 100%;
  align-items: center;
  flex-wrap: wrap;

  &__item {
    display: flex;
    padding: 0.5rem 0.75rem;
    background-color: rgba($black, 0.5);
    margin-right: 0.5rem;
    color: $white; } }

@media screen and (max-width: 1336px) {
  .blog-box {
    &__item {
      padding: 4%; } } }
