@import "./src/sass/helpers/var";

.footer {
  width: 100%;
  margin: 0 auto;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: space-between;

  &--wrapper {
    padding: 1.5rem 2.5%;
    background-color: rgba($white, 1);
    width: 100%;
    margin: 0 auto;
    display: flex;
    box-shadow: 0 0 2.5px 0 $purple;
    justify-content: space-between;
    align-items: center;
    z-index: 11; }

  &--about {
    width: 100%;
    background-color: transparent;
    bottom: 0;
    left: 0;
    box-shadow: none;
    justify-content: flex-end;

    .social-menu {
      width: 15%;
      margin-right: 2.5%; }

    .btn--order {
      width: 15%; }

    .copyright {
      margin-right: auto;
      color: $white;
      &--color {
        padding-left: 1rem;
        color: $black; } } } }

.copyright {
  width: 20%;
  display: flex; }

@media screen and (max-width: 1224px) {
  .footer {
    &--about {
      .btn--order {
        width: 20%; } } }
  @media screen and (max-width: 1024px) {
    .footer {
      &--wrapper {
        width: 100%;
        border-radius: 0;
        padding: 1rem 2.5%; }
      &--about {
        width: 100%;
        .social-menu {
          width: 20%;
          margin-right: 1%; } } } } }
